    <!-- <button class="btn btn-success btn-sm">새 포스트</button> -->
    <slot name="buttons">
        <!-- <button class="btn btn-success btn-sm">새 포스트</button> -->
    </slot>
</h1>
</template>




<script>
export default {

    name:'PageTitle',

    props:{
        'mainText' : {
            type: String
        },
        'subText':{
            type: String
        }
    },

}
</script>


