import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'


import * as Container from '@/container'
import * as BlogContainer from '@/container/blog'
import * as PhotoContainer from '@/container/photos'

Vue.use(Router)

export default new Router({
  routes: [
    
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },

    {
      path: '/about',
      name: 'About',
      component: Container.About
    },

    {
      path: '/career',
      name: 'Career',
      component: Container.Career
    },

    {
      path: '/education',
      name: 'Education',
      component: Container.Education
    },

    {
      path: '/skills',
      name: 'Skills',
      component: Container.Skills
    },

    {
      path: '/awards',
      name: 'Awards',
      component: Container.Awards
    },

    // ========== Blog ========== //
    {
      path: '/blog',
      name: 'BlogList',
      component: BlogContainer.List
    },
    {
      path: '/blog/create',
      name: 'BlogCreate',
      component: BlogContainer.Create
    },
    {
      path: '/blog/:id',
      name: 'BlogDetail',
      component: BlogContainer.Detail
    },
    // ========== Blog ========== //



    // ========== Photos ========== //
    {
      path: '/photos',
      name: 'PhotoList',
      component: PhotoContainer.List
    },
    // ========== Photos ========== //

  ]
})



