<template>
    <section class="resume-section p-3 p-lg-5 d-flex flex-column" id="education">
        
        <div class="my-auto" v-if="post == null">
            <p>Loading...</p>
        </div>
        
        <div class="my-auto" v-else>
            <page-title :main-text="post.title" sub-text="Blog">
                <button slot="buttons" class="btn btn-success btn-sm">수정</button>&nbsp;&nbsp;
                <button slot="buttons" class="btn btn-success btn-sm" @click.prevent="removeItem($route.params.id)">삭제</button>
            </page-title>
            <div class="resume-date">
                <span class="text-primary">{{ post.date }}</span>
            </div>
            
            <p class="contents" v-html="post.contents"></p>
        </div>
        
        
      </section>
</template>



<script>
import { PageTitle } from '../../components/commons'

export default{
    name: 'Blog',

    components : {
        PageTitle
    },

    data(){
        return {

            post: null

        }
    },

    created(){
        
        this.getData(this.$route.params.id)
    },

    methods: {

        // ===== 찾기 ===== //
        getData(id){
            var item = this.$firebase_db.ref('posts/'+id)
            item.on('value', data=>{
                this.$set(this, 'post', data.val())
            })
        },

        // ===== 삭제 ===== //
        removeItem(id){
            if(!confirm('해당 포스트를 삭제하시겠습니까?')){
                return
            }
            this.$firebase_db.ref('posts/'+id).remove()
            this.$router.go(-1)
        }
    },
}    
</script>



<style>
.contents img{
    width:100% !important;
}
</style>



