<template>
    <section class="resume-section p-3 p-lg-5 d-flex flex-column" id="education">
        <div class="my-auto">
            <page-title main-text="글쓰기" sub-text="new">
            </page-title>

            <form>
                <div class="form-group">
                    <label for="exampleFormControlInput1">제목</label>
                    <input type="text" class="form-control" v-model="post.title" placeholder="제목을 입력해 주세요">
                </div>
                <div class="form-group">
                    <label for="exampleFormControlSelect1">카테고리</label>
                    <select class="form-control" id="exampleFormControlSelect1" v-model="post.category">
                    <option value="">선택</option>
                    <option value="nodejs">Node.js</option>
                    <option value="vuejs">Vue.js</option>
                    <option value="angularjs">Angular.js</option>
                    <option value="javascript">Javascript</option>
                    <option value="php">PHP</option>
                    <option value="aws">AWS</option>
                    <option value="database">Database</option>
                    </select>
                </div>
                <div class="form-group">
                    <label for="exampleFormControlSelect1">본문</label>
                    <vue-editor v-model="post.contents"></vue-editor>
                </div>

                <button type="submit" class="btn btn-outline-secondary">임시저장</button>
                <button type="submit" class="btn btn-success" @click.prevent="addPost">작성완료</button>
            </form>
        
            

        </div>
      </section>
</template>



<script>
import { PageTitle } from '../../components/commons'
import { VueEditor } from 'vue2-editor'

export default{
    name: 'Blog',

    components : {
        PageTitle,
        VueEditor
    },


    data(){
        return {

            post : {
                title       : '',
                contents    : '',
                category    : '',
            },

            content: '<h1>Some initial content</h1>'  
        }
    }, 


    methods: {

        addPost(){
            for(var p of Object.values(this.post)){
                if(p == ''){
                    alert('모든 항목을 입력해 주세요')
                    return 
                }
            }
            var now = new Date()
            var today = {
                y : now.getFullYear(),
                m : (Number(now.getMonth()+1 < 10) ? ('0' + Number(now.getMonth()+1)) : Number(now.getMonth()+1)),
                d : (now.getDate() < 10) ? ('0'+ Number(now.getDate())) : Number(now.getDate())
            }
            this.post.date = today.y+'.'+today.m+'.'+today.d
            this.$firebase_db.ref('posts').push(this.post)
        }

    }


}    
</script>


