<template>
    <section id="blog" class="resume-section p-3 p-lg-5 d-flex flex-column" >
        <div class="my-auto">
            <page-title main-text="Tech" sub-text="Blog">
                <button 
                    slot="buttons" 
                    class="btn btn-success btn-sm"
                    @click.prevent="$router.push('/blog/create')">
                    새 포스트
                </button>
            </page-title>

            <div class="subheading mb-5">
                <button 
                    class="btn btn-outline-secondary" 
                    @click.prevent="changedCategory('all')"
                    v-bind:class="{'active':category=='all'}">
                    #All
                </button>
                <button 
                    class="btn btn-outline-secondary" 
                    @click.prevent="changedCategory('nodejs')"
                    v-bind:class="{'active':category=='nodejs'}">
                    #Node.js
                </button>
                <button 
                    class="btn btn-outline-secondary" 
                    @click.prevent="changedCategory('php')"
                    v-bind:class="{'active':category=='php'}">
                    #PHP
                </button>
                <button 
                    class="btn btn-outline-secondary" 
                    @click.prevent="changedCategory('vuejs')"
                    v-bind:class="{'active':category=='vuejs'}">
                    #Vue.js
                </button>
                <button 
                    class="btn btn-outline-secondary" 
                    @click.prevent="changedCategory('angularjs')"
                    v-bind:class="{'active':category=='angularjs'}">
                    #Angular.js
                </button>
                <button 
                    class="btn btn-outline-secondary" 
                    @click.prevent="changedCategory('javascript')"
                    v-bind:class="{'active':category=='javascript'}">
                    #Javascript
                </button>
                <button 
                    class="btn btn-outline-secondary" 
                    @click.prevent="changedCategory()"
                    v-bind:class="{'active':category=='aws'}">
                    #AWS
                </button>
                <button 
                    class="btn btn-outline-secondary" 
                    @click.prevent="changedCategory()"
                    v-bind:class="{'active':category=='database'}">
                    #Database
                </button>
                <button 
                    class="btn btn-outline-secondary" 
                    @click.prevent="changedCategory('etc')"
                    v-bind:class="{'active':category=='etc'}">
                    #ETC
                </button>
            </div>

            
    
            <div v-if="posts != null">
                <div 
                    class="resume-item d-flex flex-column flex-md-row mb-5" 
                    v-for="(post, pid)  in  Object.keys(posts)" 
                    :key="pid"
                    @click.prevent="$router.push('/blog/'+post)">
                    <div class="resume-content mr-auto">
                        <h3 class="mb-0">{{ posts[post].title }}</h3>
                        <div class="subheading mb-3">#{{ posts[post].category }}</div>
                        <!-- <div class="subheading mb-3">Bachelor of Science</div> -->
                        <div>
                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas ad pariatur veritatis minima ex blanditiis, iste eveniet facere ipsa...
                        </div>
                        <!-- <p>GPA: 3.23</p> -->
                    </div>
                    <div class="resume-date text-md-right">
                        <span class="text-primary">{{ posts[post].date }}</span>
                    </div>
                </div>
            </div>
            <div v-else>
                Loading...
            </div>

      

        </div>
      </section>
</template>



<script>
import { PageTitle } from '../../components/commons'


export default{
    name: 'Blog',
    firebase: {
        
        b: this.$fb_posts
        
    }, // Vuefire 라이브러리로 Vue.js속성 바인딩

    components : {
        PageTitle
    },


    data(){
        return{
            newBook: {
                id: 1,
                title: "자바스크립트 Promise 쉽게 이해하기",
                category : "javascript",
                date: '2018.05.20'
            },


            category: 'all',

            posts: null,
        }
    },

    computed:{
        // categoryBy(){
        //     if(this.category=='all' || this.category==''){
        //         return this.posts
        //     }
        //     return this.posts.filter(post=>{
        //         return post.category == this.category
        //     })
        // }
    },




    created(){
        this.getData()
        this.$set(this, 'category', this.$route.query.category?this.$route.query.category:'all')
    },

    

    updated(){
        this.$set(this, 'category', this.$route.query.category)
    },


    methods : {

        getData(){
            var bb = this.$firebase_db.ref('posts')
            var a;
            bb.on('value', data=>{
                // console.log("data.key : ", Object.keys(data.val()));
                this.$set(this, 'posts', data.val())
                // console.log("data.key : ", Object.keys(data.val()));
                // this.$set(this, 'posts', Object.values(data.val()))
            })
        },

        changedCategory(c){
            this.$set(this, 'category', c)
            this.$router.push('/blog?category='+c)
        },

    }


}    
</script>




<style>

#blog .resume-item{
    cursor: pointer;
}
#blog .resume-item:hover{
    opacity: .4;
}


</style>



